













































































































































@import './scss/mixins';

.vue-command {
  .term-ps {
    margin-right: 0.5rem;
  }

  input {
    background: none;
    border: none;
    font-family: 'Inconsolata', monospace;
    font-size: 1rem;
    outline: none;
    width: 60%;
  }

  @media only screen and (max-width: 400px) {
    input {
      width: 40%;
    }

    ::-webkit-input-placeholder {
      color: transparent;
    }
    :-moz-placeholder {
      color: transparent;
    }
    ::-moz-placeholder {
      color: transparent;
    }
    :-ms-input-placeholder {
      color: transparent;
    }
  }

  .term-stdin {
    background: none;
    margin: 0;
    border: 0;
    color: inherit;
    font-family: inherit;
    font-size: 1rem;
  }
}
